import axios from "axios";
import React, { useEffect, useState } from "react";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import { img_300, noPicture } from "../../config/config";
import "./Carousel.css";

const handleDragStart = e => e.preventDefault();

const Gallery = ({ media_type, id }) => {
    const [credits, setCredits] = useState();

    const items = credits?.map(e => (
        <div className="carouselItem">
            <img src={e.profile_path ? `${img_300}/${e.profile_path}` : noPicture} alt={e?.name} onDragStart={handleDragStart} className="carouselItem_img" />
            <p className="carouselItem_text">{e?.name}</p>
        </div>
    ));

    const responsive = {
        0: {
            items: 3,
        },
        512: {
            items: 5,
        },
        1024: {
            items: 7,
        },
    };

    const fetchData = async () => {
        const { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}/credits?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`);
        setCredits(data.cast);
    };

    useEffect(() => {
        fetchData();
        // eslint-disable-next-line
    }, []);

    return <AliceCarousel autoPlay responsive={responsive} infinite disableButtonsControls disableDotsControls mouseTracking items={items} />;
};

export default Gallery;
