import axios from "axios";
import React, { useEffect, useState } from "react";
import Genres from "../../components/Genres";
import useGenres from "../../components/hooks/useGenre";
import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";

const Series = () => {
    const [page, setPage] = useState(1);
    const [content, setContent] = useState([]);
    const [numOfPages, setNumOfPages] = useState();
    const [selectedGenres, setselectedGenres] = useState([]);
    const [genres, setgenres] = useState([]);
    const genreForUrl = useGenres(selectedGenres);

    const fetchSeries = async () => {
        const { data } = await axios.get(`https://api.themoviedb.org/3/discover/tv?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&sort_by=popularity.desc&include_adult=true&include_video=false&page=${page}&with_genres=${genreForUrl}`);
        setContent(data.results);
        setNumOfPages(data.total_pages);
    };

    useEffect(() => {
        fetchSeries();
        window.scroll(0, 0);
        // eslint-disable-next-line
    }, [page, genreForUrl]);

    return (
        <div>
            <span className="pageTitle">TV Shows</span>
            <Genres type="tv" selectedGenres={selectedGenres} setselectedGenres={setselectedGenres} genres={genres} setGenres={setgenres} setPage={setPage} />
            <div className="trending">{content && content.map(c => <SingleContent key={c.id} id={c.id} poster={c.poster_path} title={c.name} date={c.first_air_date} media_type="tv" vote_average={c.vote_average} />)}</div>
            {numOfPages > 1 && <CustomPagination setpage={setPage} numOfPages={numOfPages} />}
        </div>
    );
};

export default Series;
